.top {
  padding: 20px;


  h3 {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 130%;
    color: #FFFFFF;
  }

  p {
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 140%;
    color: #ABBBC2;
  }

  input {
    background: #2D303E;
    /* Base / Dark Line */

    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    outline: none;
    padding: 14px;
    max-width: 297px;
    width: 298px;
  }

  .right-button2 {
    padding: 12px;
    background: #2D303E;
    color: #ffff;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    width: 48px;
    height: 48px;
    margin-right: 21px;
  }

  span {
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 140%;
    text-align: right;
    color: #FFFFFF;
  }

  .top1 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 15px;

    .top1-left {
      display: flex;
      align-items: center;

    }
  }

  .top1-left-left {
    .imagex {
      margin-right: 10px;
      width: 38px;
      height: 38px;
    }
  }

  .top2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 25px;

    button {
      outline: none;
      background: #1F1D2B;
      padding: 8px;
      border: 1px solid #EA7C69;
      border-radius: 8px;
    }
  }
}