body {
  margin: 0;
  padding: 0;
  font-family: 'Barlow', sans-serif;
  background: #252836;
  user-select: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
a {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

.App {
  display: flex;
}
.menu {
  display: block;

}

.main {
  display: flex;
  align-items: center;
}

#root {
  display: flex;
}

.headerrTop {
  display: flex;
}

.cardsSection {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 80%;
  height: 348px;

  overflow: scroll;
  padding-top: 50px;
  padding-bottom: 0;
  margin-bottom: 0;

  &::-webkit-scrollbar {
    width: 0;
    height: 0;
  }
}

.comp {
  display: flex;
}