.sidebar {
  background: #1F1D2B;
  width: 108px;

  .sidebar-info {
    height: 100vh;
    color: white;
    padding: 20px;
    top: 0;
    width: 100%;
    margin-bottom: 20px;
    z-index: 4;
    display: block;
  }

  .link {
    transition: all ease 0.5s;
    display: block;
    font-weight: bold;
    color: black;
    padding: 17px;
    text-decoration: none;
    &:hover{
      background: #252836;
      border-radius: 12px 0px 0px 12px;
      border-radius: 8px;
      .icon{
        fill:white;
        background: #EA7C69;
        box-shadow: 0px 8px 24px rgba(234, 124, 105, 0.32);
        border-radius: 8px;
      }
    }
  }
  .icon{
    fill: #EA7C69;
    padding: 15px;

    border-radius: 8px;
  }
}