.header-top {
  display: flex;
  justify-content: space-between;
  padding: 32px;
  align-items: center;
  

  h2 {
    font-style: normal;
    font-weight: 600;
    font-size: 28px;
    line-height: 140%;
    /* or 39px */


    /* White */

    color: #FFFFFF;
  }

  h4 {
    font-style: normal;
    font-weight: normal;
    font-size: 16px;
    line-height: 140%;
    color: #E0E6E9;
  }
  .imag{
      width: 20px;
      height: 20px;
      position: absolute;
      background-size: cover;
      top: 12px;
      bottom: 0;
      left: 10px;
  }
  input {
    background: #2D303E;
    border: 1px solid #393C49;
    box-sizing: border-box;
    border-radius: 8px;
    padding: 14px 14px 14px 38px ;
    outline: none;
    border-radius: 12px;
  }
}
.header-top-right{
  position: relative;
}